
import footerNav from '@/components/footerNav/footerNav.vue';
import Swiper from 'vux/src/components/swiper/swiper.vue'

import { getIndexBanner, getWxSettings, getNearStore, getMyShop } from '@/api/index.js';
const wx = require('weixin-js-sdk');

export default {
name: 'Index',
data() {
return {
userType: this.$store.state.user.userType,
myShop: null,
myShopIcon01: require('../../assets/baiye/myshop1.png'),
myShopIcon02: require('../../assets/baiye/myshop2.png'),
icon: require('../../assets/baiye/icon01@2x.png'),
lists: [],
banners: []
};
},
mounted() {
console.log(this.userType)











this.getNearStore();
this.getMyShop();
getIndexBanner().then(res => {
if (res.data.code == 1) {
let resData = res.data.data;
this.banners = resData.map(item => ({
img: item.img_url,
url: item.url
}));
}
});
},
methods: {

getNearStore() {
getNearStore({
latitude: 34.79977,
longitude: 113.66072
}).then(res => {
console.log(res)
if (res.data.code == 1) {
this.lists.push(...res.data.data);
}
})
},

getMyShop() {
getMyShop({
lat: 34.79977,
lng: 113.66072,
user_id: this.$store.state.user.userid,
type: 2
}).then(res => {
if (res.data.code == 1) {
let resData = res.data.data;
let myShop = {
shop_avatar: resData.shop_avatar,
month_sell: resData.sell_num,
distance: resData.distance,
coordinate_address: resData.coordinate_address,
name: resData.name,
id: resData.id,
distance: resData.juli
};
this.myShop = myShop;
}
});
}
},
components: {
footerNav,
Swiper,
}
};




