export default {
  props: {
    msg: String,
    icon: String,
    timeout: Number,
    callback: Function
  },
  computed: {
    iconsClass() {
      let _icon = '';
      if (this.icon === 'success' || this.icon === 'error') {
        _icon = 'mj-toast-' + this.icon + '-icon';
      }
      return _icon;
    }
  }
};



